@import "src/styles/global.scss";
.banner-promo {
  font-family: $fontFamilyNunitoSans;
  font-style: $fontStyle;
  font-weight: $fontWeight;
  font-size: $fontSize;
  color: $fontColor;
  display: flex;
  column-gap: 20px;

  &__title {
    font-family: $fontFamilyUnbounded;
    font-size: 36px;
    font-style: normal;
    font-weight: 500;
    line-height: 44px;
    max-width: 544px;
    margin: 0;
    display: flex;
    align-items: center;
  }

  &__main {
    max-width: 1108px;
    height: 400px;
    width: 100%;
    display: flex;
    justify-content: end;
    border: 1px solid $grayLightColor;
    border-radius: 20px;
    column-gap: 48px;
    overflow: hidden;
    min-width: 768px;
  }

  &__figure-images {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(10, 1fr);
    max-width: 351px;
    max-height: 400px;
    width: 100%;
    flex-shrink: 0;
  }

  &__animation {
    animation: slideDownAnimation 1s ease forwards;
  }

  &__image {
    &_box1 {
      grid-column: 1/span 2;
      grid-row: 5/span 3;
      align-self: end;
    }

    &_box2 {
      grid-column: 4/span 2;
      grid-row: 6/span 2;
      justify-self: start;
    }

    &_box3 {
      grid-column: 3/span 2;
      grid-row: 3/span 2;
      justify-self: end;
      align-self: end;
    }

    &_polygon {
      grid-column: 1/span 2;
      grid-row: 1/span 3;
    }
  }
}

@keyframes slideDownAnimation {
  0% {
    transform: translateY(-100%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}
